import React from "react";
import Button from "./Button";
import "./Home.css";

const Home = () => {
  return (
    <div className="home_wrapper">
      <h1>KM</h1>
      <h2>Przewozy autokarowe</h2>
      <p>Krzysztof Majewski</p>
      <div className="buttons">
        <a href="#bus">
          <Button ButtonStyles="btn_full">Autobusy</Button>
        </a>
        <a href="#contact">
          <Button ButtonStyles="btn_border">Kontakt</Button>
        </a>
      </div>
    </div>
  );
};

export default Home;
