import React from "react";
import "./Button.css";

const STYLES = ["btn_full", "btn_border"];

const scrol = document.getElementsByClassName("bus_section").bus;
// const top = scrol.offsetTop;

const Button = ({ ButtonStyles, children }) => {
  const styles = STYLES.includes(ButtonStyles) ? ButtonStyles : STYLES[0];

  return <button className={styles}>{children}</button>;
};

export default Button;
